"use client"

import { useState } from "react"
import { useRealtime } from "@/contexts/RealtimeContext"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"

export function RealtimeTest() {
  const { settings, updateSettings, isConnected } = useRealtime()
  const [testName, setTestName] = useState("")

  const handleTestUpdate = () => {
    if (testName.trim()) {
      updateSettings({ websiteName: testName })
      setTestName("")
    }
  }

  return (
    <Card className="w-full max-w-md">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="text-lg">Real-time Test</CardTitle>
          <Badge variant={isConnected ? "default" : "destructive"}>
            {isConnected ? "Connected" : "Disconnected"}
          </Badge>
        </div>
        <CardDescription>
          Test real-time updates between admin and website
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div>
          <p className="text-sm font-medium mb-2">Current Website Name:</p>
          <p className="text-lg font-bold text-primary">{settings.websiteName}</p>
        </div>
        
        <div>
          <p className="text-sm font-medium mb-2">Current Tagline:</p>
          <p className="text-sm text-muted-foreground">{settings.tagline}</p>
        </div>

        <div className="space-y-2">
          <input
            type="text"
            placeholder="Enter new website name"
            value={testName}
            onChange={(e) => setTestName(e.target.value)}
            className="w-full px-3 py-2 border border-border rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary"
          />
          <Button 
            onClick={handleTestUpdate} 
            disabled={!testName.trim() || !isConnected}
            className="w-full"
          >
            Update Website Name (Real-time)
          </Button>
        </div>

        <div className="text-xs text-muted-foreground">
          <p>• Open the website in another tab</p>
          <p>• Change the name here</p>
          <p>• Watch it update instantly on the website</p>
        </div>
      </CardContent>
    </Card>
  )
}
